import React from 'react';
import KeyboardEventHandler from 'react-keyboard-event-handler';
import {DataService} from '../services/DataService';
//import process from 'process';

class Home extends React.Component {
    constructor(props) {
        super(props);
        this.resetCounter = this.resetCounter.bind(this)

        this.state = { handleKeys : ['enter','z','c'/*'left','right',*/], coaches:[] }
        this.props.parent.currentPage = this;
        this.props.parent.setState({"availableKeys" : this.state.handleKeys, coachid:null, cid:null, ccid:null})

        this.buttons={}
    }

    resetCounter(){
        //this.setState({"countDown":30})
    }

    async componentDidMount() {
        await DataService.loadJSON()
        this.setState({coaches: DataService.getCoachList()})
        //console.log( DataService.getCoachList() )
        window.setElementEventLintener("Home")
        //this.button.focus();
        var keys = Object.keys(this.buttons);
            if (keys.length > 0)
                this.buttons[keys[0]].focus();
    }

    componentWillUnmount(){
    }

    render(){
        return (<KeyboardEventHandler
                handleKeys={this.state.handleKeys}
                onKeyEvent={(key, e, page) => this.props.util(key, e, this, page)} >
                <div style={{backgroundImage:'url(./assets/v_bg.jpg)', width:1080, height:1920}}>
                    {/*
                    <div className="home_pageTitle">
                        智慧健身鏡
                    </div>
                    <div className="home_pageTitle2">
                        和你一起做運動
                    </div>
                    */}
                    <div className="home_selectCoach">
                        請選擇教練
                    </div>

                    <div className="home_selectCoachAreaSize" >
                        {
                            this.state.coaches.map((item,index) => {
                                var attr = '{"coachid":"'+item.id+'"}'
                                //let avatar_url = "url(./course"+item.avatar+")"
                                let avatar_url = "./course"+item.avatar
                                return <div className="home_coachArea"
                                            tabIndex={index}
                                            key={index}
                                            attr={attr}
                                            row_index={0}
                                            col_index={index}
                                            col_max={2}
                                            ref={(input) => { this.buttons[item.id] = input }} href="#"
                                        >

                                            <div className="home_videoArea" >
                                                <img src={avatar_url} alt="coach" style={{height:1000}} ></img>
                                            </div>
                                            {/*
                                            <div className="home_videoArea" style={{backgroundImage: avatar_url,
                                                backgroundPosition:"center",
                                                //backgroundRepeat:"noRepeat",
                                                backgroundRepeat:"no-repeat",
                                                backgroundSize:"auto 1000px"}} > </div> */}
                                            {/*<video className="home_videoArea" autoPlay={true} loop={true}>
                                                <source src={"./course"+item.avatar_video} type="video/mp4" />
                                            </video>
                                            <div className="home_coach_title">{item.name}</div>
                                            */}
                                    </div>
                            })
                        }
                    </div>



                    <div className="home_image1"></div>
                    <div className="home_textArea1"><div style={{color:"#17FF21"}}>點選</div>雙手彎舉</div>

                    <div className="home_image2"></div>
                    <div className="home_textArea2"><div style={{color:"#17FF21"}}>前一頁</div>雙手交叉</div>

                    <div className="home_image3"></div>
                    <div className="home_textArea3"><div style={{color:"#17FF21"}}>向上選取</div>左手彎舉</div>

                    <div className="home_image4"></div>
                    <div className="home_textArea4"><div style={{color:"#17FF21"}}>向下選取</div>右手彎舉</div>

                </div>
            </KeyboardEventHandler>)
    }
}

export default Home;

