import React from 'react';
import KeyboardEventHandler from 'react-keyboard-event-handler';
//import {Fire,Heart,Heart2} from '../icon/index'
import { CircularProgressbarWithChildren, buildStyles } from 'react-circular-progressbar';
//import 'react-circular-progressbar/dist/styles.css';

import {DataService} from '../services/DataService';

class ClassBegin extends React.Component {
    constructor(props) {
        super(props);
        this.resetCounter = this.resetCounter.bind(this)
        this.getExerciseWebSockit = this.getExerciseWebSockit.bind(this)

        this.app = this.props.parent;
        //console.log( this.app.state.isUseWebSocket )
        this.state = { 'handleKeys' : ['enter','left'],
                        'beforeStartTime' : new Date().getTime(),
                        'startTime': null,
                        'intervalCount' : 0,
                        'course':null,"countDown":3,"timer":"00:06","newtimer":"","start":false,"counter":0,
                        "isShowTip":true,'tip':'雙腳打開與肩同寬，雙手抓取啞呤慢慢彎曲手臂向上彎舉。',
                        "isHold":false,"holdSec":2,"currentHold":0,"startHoldTime":0,"holdSecPreson":1,
                        "isShowNotice":false, 'notice':'左肩膀移動了，請回到預備動作重新開始!',
                        "noticeHoldSec":2,"noticeCurrentHold":0,"noticeStartHoldTime":0,"noticeHoldSecPreson":1,
                        "quit_time" : null,
                        "actionCountDown": 6,
                        "stopSecPreson" : 0 ,"stopSec":0,
                        "responseBody": null}
        this.props.parent.currentPage = this;
        this.props.parent.setState({"availableKeys" : this.state.handleKeys})

        this.percentage = 30
    }

    gotoNextPage(){
        let user = this.app.state.user;
        user['timer'] = this.state.timer;
        //user['count'] = 6;
        user['end_time'] = this.DateTimeFormat(new Date(),"yyyy/MM/dd hh:mm:ss")
        user['elapsed_time'] = ( Date.parse(user.end_time) - Date.parse(user.start_Time) ) / 1000
        this.app.setState({"user":user})

        if (user.count > 0)
            this.app.changePage( this.app.state.page+1 )
        else
            //this.app.changePage( 3 )
            this.app.changePage( 2 )
    }

    resetCounter(){
    }

    DateTimeFormat(date,fmt) { //author: meizz
        var o = {
            "M+": date.getMonth() + 1, //月份
            "d+": date.getDate(), //日
            "h+": date.getHours(), //小时
            "m+": date.getMinutes(), //分
            "s+": date.getSeconds(), //秒
            "q+": Math.floor((date.getMonth() + 3) / 3), //季度
            "S": date.getMilliseconds() //毫秒
        };
        if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
        for (var k in o)
            if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
        return fmt;
    }

    getExerciseWebSockit(value){
        this.setState({"responseBody":value})
    }

    async componentDidMount() {
        window.setElementEventLintener("ClassBegin")
        var course = await DataService.getCourse(this.app.state.coachid,this.app.state.cid,this.app.state.ciid);
        this.setState({"course": course ,
                       "countDown":3,"timer":"00:03","start":false})

        this.app.setState({"user":{
            "course" : course,
            'count' : 0,
            'countTimes' : []
        }})


        this.video.onplaying = function(event) {
            event.target.pause();
            let videoHeight = event.target.videoHeight;
            let videoWidth = event.target.videoWidth;
            event.target.setAttribute("style",'left:'+  (( 1080 - (1450*videoWidth/videoHeight) ) / 2) +'px; ')
        };

        var interval = window.setInterval(async()=>{
            let canvaies = window.$.find('canvas');
            if (canvaies.length>0){
                canvaies.forEach(function(value){
                        value.remove();
                });
            }

            let responseBody = null
            let responseState = false;
            if (this.app.state.isUseWebSocket){
                responseBody = this.state.responseBody
                responseState = responseBody === null;
            } else {
                let response = null;
                try{
                    response = await fetch('http://127.0.0.1:5500/getExercise', {
                        method: 'GET',
                        mode: 'cors',
                        //credentials: 'include',
                        crossDomain:true,
                        headers:{
                            Accept: '*/*',
                            'Content-Type': 'application/json;charset=UTF-8'
                        },
                    });
                }catch(error){
                    //console.log(error)
                }
                responseState = response === null || !response.ok;
                if  (!responseState){
                    var body = await response.text();
                    responseBody = JSON.parse( body );
                }

            }

            let nowTime = new Date().getTime()

            //if (false){
            //if (response === null ||  !response.ok) {
            //console.log("interval responseState",responseState)
            if (responseState){
                /*let doHold = this.state.isHold;
                let doNoticeHold = this.state.isShowNotice;
                let startHoldTime = this.state.startHoldTime;
                let noticeStartHoldTime = this.state.noticeStartHoldTime;
                let currentHoldTime = new Date().getTime();

                //console.log( (nowTime - this.state.beforeStartTime) )
                if (((nowTime - this.state.beforeStartTime) >= 5 * 1000) && ((nowTime - this.state.beforeStartTime) < 5.5 * 1000)) {
                    doHold = true;
                    this.setState({"isShowNotice": false,
                            "isHold":true,"startHoldTime":nowTime,"currentHold":2})
                }

                if (((nowTime - this.state.beforeStartTime) >= 6 * 1000) && ((nowTime - this.state.beforeStartTime) < 6.5 * 1000)) {
                    doNoticeHold = true;
                    this.setState({"isShowNotice": true, //"notice": this.state.n,
                    "noticeStartHoldTime":nowTime,"noticeCurrentHold":2})
                }

                if (doHold){
                    let _holdSec = 2*1000
                    let step1 = startHoldTime + _holdSec;
                    let step2 = step1 - currentHoldTime;
                    let step3 = step2 / _holdSec
                    this.setState({"currentHold" : Math.ceil( step2/ 1000), "holdSecPreson": step3,"isHold":step3 > 0})
                }

                if (doNoticeHold){
                    let _holdSec = this.state.noticeHoldSec*1000
                    let step1 = noticeStartHoldTime + _holdSec;
                    let step2 = step1 - nowTime;
                    let step3 = step2 / _holdSec
                    this.setState({"isShowNotice":step3 > 0})
                }*/

                if ((nowTime - this.state.beforeStartTime) >= 30 * 1000) {
                    console.log( "response not ok.....30秒")
                    //this.app.changePage( 1 )
                } else {
                    this.setState({"stopSecPreson": (nowTime - this.state.beforeStartTime)/1000/30 })
                    this.setState({"stopSec": Math.ceil( (nowTime - this.state.beforeStartTime)/ 1000) })
                }

            } else {
                let doHold = this.state.isHold;
                let doNoticeHold = this.state.isShowNotice;

                let startHoldTime = this.state.startHoldTime;
                let noticeStartHoldTime = this.state.noticeStartHoldTime;
                let currentHoldTime = new Date().getTime();


                var value = responseBody

                //console.log("interval doHold, doNoticeHold, startHoldTime, noticeStartHoldTime, currentHoldTime, value >> ",doHold, doNoticeHold, startHoldTime, noticeStartHoldTime, currentHoldTime, value)

                //console.log("interval value.start",value.start);
                if ( !value.start ){ // 姿勢設定
                    let tip = '';
                    value.tip.note.forEach(function(val){
                        tip += (tip.length > 0 ? '<br/>' : '') + val
                    });

                    this.setState({"isShowTip": (tip.length > 0),
                                "tip" : tip,
                                "holdSec": value.tip.duration})

                    if ((nowTime - this.state.beforeStartTime) >= 30 * 1000) {
                        console.log( "response not ok.....30秒")
                        this.app.changePage( 1 )
                    } else {
                        this.setState({"stopSecPreson": (nowTime - this.state.beforeStartTime)/1000/30 })
                        this.setState({"stopSec": Math.ceil( (nowTime - this.state.beforeStartTime)/ 1000) })
                    }

                } else { //開始倒數
                    this.setState({"isShowTip": false})
                    if (this.state.startTime == null)
                        this.setState({"startTime":nowTime})

                    if (this.state.intervalCount % 10 === 0){
                        if ( this.state != null && this.state.course != null ){
                            var newCountDown = this.state.countDown

                            if (this.state.start === false){
                                newCountDown = newCountDown-1
                                this.setState({"stopSecPreson":0, "stopSec": 0});

                                if (newCountDown === 0){
                                    this.setState({ "start":true})

                                    let user = this.app.state.user;
                                    user['start_Time'] = this.DateTimeFormat(new Date(),"yyyy/MM/dd hh:mm:ss")
                                    this.app.setState({"user":user})
                                    this.transition.setAttribute("style",'display:block; ')
                                    this.transitionImg.setAttribute("src",'')
                                    this.transitionImg.setAttribute("src","./assets/start.gif")
                                    this.transitionImg.setAttribute("style",'display:block; ')
                                }
                            }else{
                                this.transition.setAttribute("style",'display:none; ')
                                this.transitionImg.setAttribute("src",'')
                                this.transitionImg.setAttribute("style",'display:none; ')
                                newCountDown = newCountDown+1
                            }
                            newCountDown = newCountDown < 0 ? 0  : newCountDown

                            var h = parseInt( Math.abs(newCountDown) / 3600 )
                            var m = parseInt( Math.abs(newCountDown) / 60 )
                            var s = Math.abs(newCountDown) % 60
                            var timer = (h > 0 ? (h<10 ? "0"+h : h )+":"  :'')+(m<10 ? "0"+m : m ) +":"+( s< 10 ?"0"+s : s)
                            this.setState({ "timer": timer, "countDown": newCountDown, "newtimer":s})

                            /*if (doHold){
                                this.setState({"currentHold":this.state.currentHold-1})
                            }*/
                        }
                    }

                    if (this.state.start){
                        if (this.video.paused)
                            this.video.play();

                        let user = this.app.state.user;

                        this.setState({"actionCountDown" : 6 - value.action.times})

                        if (value.action.times > user['count']){
                            user['count'] = value.action.times;
                            let beginTime = Date.parse(value.action.startPoint)
                            let endTime = Date.parse(value.action.startPointLastTime)
                            user['countTimes'].push(endTime - beginTime)

                            this.app.setState({"user":user})

                            if ( 6 - value.action.times === 0){
                                console.log("完成 六 次...")
                                this.gotoNextPage();
                            }
                        }

                        if (value.action.alertLastTime !== "" ){
                            let lastTime = Date.parse(value.action.alertLastTime)
                            if ((nowTime - lastTime) < 0.5 * 1000) {
                                let alertMessage = '';
                                value.action.alert.forEach(function(val){
                                    alertMessage += ( alertMessage.length>0 ? '<br/>' : '') +val;
                                });

                                doNoticeHold = true;
                                this.setState({"isShowNotice": true, "notice": alertMessage,
                                "noticeStartHoldTime":nowTime,"noticeCurrentHold":2})
                            }
                        }

                        if (value.action.stop === true){
                            this.setState({"isHold":false})
                            doHold = false;
                        }

                        if (value.action.startPoint !== "" ){
                            let lastTime = Date.parse(value.action.startPoint)
                            if ((nowTime - lastTime) < 0.5 * 1000) {
                                this.setState({"stopSecPreson":0, "stopSec": 0});

                                doHold = true;
                                doNoticeHold = false;
                                this.setState({"isShowNotice": false,
                                        "isHold":true,"startHoldTime":nowTime,"currentHold":this.state.holdSec})
                                currentHoldTime = nowTime;
                            }
                        }

                        if (value.action.startPointLastTime !== "" ){
                            let lastTime = Date.parse(value.action.startPointLastTime)
                            /*if ((nowTime - lastTime) >= 30 * 1000) {
                                console.log("超過 30秒...")
                                this.gotoNextPage();
                            }*/
                            if ((nowTime - lastTime) >= 30 * 1000) {
                                console.log("超過 30秒...")
                                this.gotoNextPage();
                            } else {
                                this.setState({"stopSecPreson": (nowTime - lastTime)/1000/30 })
                                this.setState({"stopSec": Math.ceil( (nowTime - lastTime)/ 1000) })
                            }
                        } else {
                            if ((nowTime - this.state.startTime) >= 30 * 1000) {
                                console.log( "No action any one .....30秒")
                                this.app.changePage( 1 )
                            } else {
                                this.setState({"stopSecPreson": (nowTime - this.state.startTime)/1000/30 })
                                this.setState({"stopSec": Math.ceil( (nowTime - this.state.startTime)/ 1000) })
                            }
                        }

                        if (value.action.quit){
                            let lastTime = nowTime
                            if (this.state.quit_time != null){
                                lastTime = this.state.quit_time
                            } else {
                                this.setState({"quit_time" : nowTime})
                            }

                            doNoticeHold = true;
                            let alertMessage = "三秒後離開"
                            this.setState({"isShowNotice": true, "notice": alertMessage,
                                "noticeStartHoldTime":nowTime,"noticeCurrentHold":3})

                            if ((nowTime - lastTime) >= 3 * 1000) {
                                this.gotoNextPage();
                            }
                        } else {
                            this.setState({"quit_time" : null})
                            if (doNoticeHold){
                                if (this.state.notice === "三秒後離開"){
                                    doNoticeHold = false;
                                    this.setState({"isShowNotice": false});
                                }
                            }
                        }
                    }

                    if (doHold){
                        let _holdSec = this.state.holdSec*1000
                        let step1 = startHoldTime + _holdSec;
                        let step2 = step1 - currentHoldTime;
                        let step3 = step2 / _holdSec
                        this.setState({"currentHold" : Math.ceil( step2/ 1000),
                                    "holdSecPreson": step3,"isHold":step3 > 0})
                    }

                    if (doNoticeHold){
                        let _holdSec = this.state.noticeHoldSec*1000
                        let step1 = noticeStartHoldTime + _holdSec;
                        let step2 = step1 - nowTime;
                        let step3 = step2 / _holdSec
                        this.setState({"isShowNotice":step3 > 0, "isHold":false})
                    }

                    this.setState({'intervalCount':this.state.intervalCount+1})
                }
            }
        }, 100)
        this.setState({"interval":interval})

        this.app.setState({"doExercise":true})
    }

    async componentWillUnmount(){
        this.app.setState({"doExercise":false})

        window.clearInterval( this.state.interval )
        if (this.props.parent.state.isDeveMode === false){
            if (this.app.state.isUseWebSocket){
                this.app.ws.send( JSON.stringify({"function":"endExercise"} ))
            } else {
                try{
                    var response = await fetch('http://127.0.0.1:5500/endExercise', {
                        method: 'GET',
                        mode: 'cors',
                        //credentials: 'include',
                        crossDomain:true,
                        headers:{
                            Accept: '*/*',
                            'Content-Type': 'application/json;charset=UTF-8'
                        },
                    });
                    var body = await response.text();
                        console.log( "endExercise:", body );
                }catch(error){
                    console.log(error)
                }
            }
        }
    }


    render(){
        return (<KeyboardEventHandler
                handleKeys={this.state.handleKeys}
                onKeyEvent={(key, e, page) => this.props.util(key, e, this, page)} >
                <div style={{backgroundImage:'url(./assets/v_bg.jpg)', width:1080, height:1920}}>
                    <div className="classBegin_backgroundImg" ></div>

                    <div className="chanetransition"
                        ref={(input) => { this.transition = input; }} ></div>
                    <div className="classBegin_ClassName">
                    { this.state != null && this.state.course != null ? this.state.start ? this.state.course.title : "<請就預備姿勢>" : null}
                    </div>
                    <div className="classBegin_Timer">
                        { this.state != null && this.state.timer != null ? this.state.start ? this.state.timer : "" : null}
                    </div>
                    <div className="classBegin_newTimer">{ this.state != null && this.state.newtimer != null ? !this.state.start ? this.state.newtimer : "" : null}</div>

                    <div className="classBegin_circleProgress"
                        style={{display: this.state.isHold ? 'block' : 'none' }}
                    ref={(input) => { this.circleprogress = input; }}
                    >
                        <CircularProgressbarWithChildren
                            value={ parseInt(this.state.holdSecPreson * 100 )}
                            background
                            styles={buildStyles({
                                strokeLinecap: "butt",
                                pathColor: '#17FF21',
                                trailColor: 'rgba(214, 214, 214, 0.6)',
                                backgroundColor: 'rgba(0, 0, 0, 0.7)',
                            })}
                            strokeWidth={5}
                            >
                            <div className="classBegin_circleProgress_title">動作</div>
                            <div className="classBegin_circleProgress_value">{this.state.currentHold}</div>
                            <div className="classBegin_circleProgress_sec">秒</div>
                        </CircularProgressbarWithChildren>
                    </div>

                    <div className="classBegin_stopCircleProgress">
                    <CircularProgressbarWithChildren
                            value={ parseInt(this.state.stopSecPreson * 100 ) }

                            styles={buildStyles({
                                strokeLinecap: "butt",
                                pathColor: '#17FF21',
                                trailColor: 'rgba(214, 214, 214, 0.6)',
                            })}
                            strokeWidth={5}
                            >

                            <div className="classBegin_stopCircleProgress_value">{this.state.stopSec}</div>

                        </CircularProgressbarWithChildren>
                    </div>

                    <div className="classBegin_counter" style={{display: this.state.start ? 'block' : 'none' }}></div>
                    <div className="classBegin_SET_Title" style={{display: this.state.start ? 'block' : 'none' }}>剩餘次數</div>
                    <div className="classBegin_SET_Value" style={{display: this.state.start ? 'block' : 'none' }}>{this.state.actionCountDown}</div>

                    <img className="classBegin_StartImg" src=""
                        style={{display:'none'}}
                        ref={(input) => { this.transitionImg = input; }} alt=""></img>

                    <video controls={false} loop={true} autoPlay={true} className="classBegin_video" ref={(video) => { this.video = video; }}>
                        <source src={this.state != null && this.state.course != null ? '/course'+this.state.course.video : null} />
                    </video>

                    <div className="classBegin_HoldImg" style={{display: this.state.isHold ? 'none'/*'block'*/ : 'none' }}>
                        <div className="classBegin_HoldImg_Title">動作時間</div>
                        <div className="classBegin_HoldImg_value_area" >
                        <div className="classBegin_HoldImg_Value">{this.state.currentHold}</div><div className="classBegin_HoldImg_Sec">秒</div>
                        </div>
                        <div className="classBegin_HoldImg_progressBar_base" >
                            <div className="classBegin_HoldImg_progressBar_value" style={{width: 597 * this.state.holdSecPreson }}></div>
                        </div>
                    </div>

                    <div className="classBegin_noticeboard" style={{display: this.state.isShowNotice ? 'block' : 'none' }}>
                        <div className="classBegin_noticeboard_title">{this.state.notice}</div>
                    </div>

                    <div className="classBegin_actionTip" style={{display: this.state.isShowTip ? 'block' : 'none' }}>
                        <div className="classBegin_actionTip2" >
                            <div className="classBegin_actionTip_img"></div>
                            <div className="classBegin_actionTip_text">{this.state.tip}</div>
                        </div>
                    </div>

                    <div className="classBegin_QuickImage" style={{display: this.state.start ? 'block' : 'none' }}></div>
                    <div className="classBegin_QuickTextArea" style={{display: this.state.start ? 'block' : 'none' }}><div style={{color:"#17FF21"}}>離開</div>雙手交叉(持續3秒)</div>

                </div>
            </KeyboardEventHandler>)
    }
}

export default ClassBegin;
