import React from 'react';
import './App.css';

//import {DataService} from './services/DataService';
import tools from './pages/util.js'

import Home from './pages/Home.js'
//import Catalog from './pages/Catalog.js'
//import Level from './pages/Level';
//import ClassSelect from './pages/ClassSelect';
//import ClassIntro from './pages/ClassIntro';
import ClassBegin from './pages/ClassBegin';
import Class2 from './pages/Class2';
import ClassDone from './pages/ClassDone';

class App extends React.Component {

  constructor(props) {
    super(props);

    this.handleChange = this.changePage.bind(this);
    this.state = {page: 1,availableKeys:[],"lastActionTime":"", isDeveMode:false,
                  doExercise:false, isUseWebSocket: true, websocip:'127.0.0.1:5500'} ;

    this.updateState = this.updateState.bind(this);
    this.useWebSocket = this.useWebSocket.bind(this);
    this.changePage = this.changePage.bind(this);
    this.handleServerItemsLoad = this.handleServerItemsLoad.bind(this);
    this.currentPage = null;
  }

  componentDidMount() {
    if ( this.state.isDeveMode === false){
      if (this.state.isUseWebSocket){
        this.useWebSocket(this)
      } else {
        if (this.interval)
          clearInterval(this.interval);
        this.interval = setInterval(this.handleServerItemsLoad, 100,this);
      }
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if ( this.state.isDeveMode === false){
      if (this.state.isUseWebSocket){
        this.useWebSocket(this)
      } else {
        clearInterval(this.interval);
        this.interval = setInterval(this.handleServerItemsLoad, 100,this);
      }
    }
  }
  componentWillUnmount() {
    if ( this.state.isDeveMode === false){
      if (this.state.isUseWebSocket){
      } else {
        clearInterval(this.interval);
      }
    }
  }

  handleServerItemsLoad = async (_app) =>{
    try{
      var response = await fetch('http://127.0.0.1:5500/exercise_status', {
        method: 'GET',
        mode: 'cors',
        //credentials: 'include',
        crossDomain:true,
        headers:{
          Accept: 'application/json',
          'Content-Type': 'application/x-www-form-encoded'
        }
      });
      if (!response.ok) throw new Error(response.statusText)
      var body = await response.text();
      var value = JSON.parse( body );
      if ( value["最後動作"].length > 0 && value["最後動作時間"].length> 0) {
        if (_app.state.lastActionTime !== value["最後動作時間"]){
          _app.setState({"lastActionTime":value["最後動作時間"]})
          _app.updateState(body)
        }
      }
    }catch(err){}
  }

  keyEvent = ((key, e, page) => tools.Util.KeyboardEventHandler_onKeyEvent(key, e,this,page))

  useWebSocket(app){
    let _app = this

    if (this.ws == null){
      this.ws = new WebSocket("ws://"+_app.state.websocip+"/api");

      this.ws.onopen = function() {
        console.log("onopen")
      };

      this.ws.onmessage = function(e) {
        console.log("onmessage: " + e.data)

        //_app.useWebSocket(e.data);
        let json = JSON.parse(e.data)
        console.log( 'function === exercise_status', json['function'] === 'exercise_status' )
        if (json['function'] === 'exercise_status') {
          var key = json['最後動作']
          console.log('最後動作', key)
          var currentFoucs = window.$(':focus')[0]
          console.log('currentFoucs', currentFoucs)
          switch(key){
            case "左手彎舉":
              console.log('case 左手彎舉')
              if (_app.state.availableKeys.includes('s')){ //up
                console.log('case 左手彎舉 in if')
                //window.lastTime = currentTime
                _app.keyEvent('s', {"target":currentFoucs},_app.currentPage)
              }
              break;
            case "右手彎舉":
              console.log('case 右手彎舉')
              if (_app.state.availableKeys.includes('x')){ //down
                console.log('case 右手彎舉 in if')
                //window.lastTime = currentTime
                _app.keyEvent('x', {"target":currentFoucs},_app.currentPage)
              }
              break;
            case "雙手交叉":
              console.log('case 雙手交叉')
              if (_app.state.availableKeys.includes('esc')){
                console.log('case 雙手交叉 in if')
                //window.lastTime = currentTime
                _app.keyEvent('esc', {"target":currentFoucs},_app.currentPage)
              }
              break;

            case "向左選取":
              console.log('case 向左選取')
              if (_app.state.availableKeys.includes('z')){ // 'left'
                console.log('case 向左選取 in if')
                _app.keyEvent('z', {"target":currentFoucs},_app.currentPage)
              }
              break;
            case "向右選取":
              console.log('case 向右選取')
              if (_app.state.availableKeys.includes('c')){ // right
                console.log('case 向右選取 in if')
                _app.keyEvent('c', {"target":currentFoucs},_app.currentPage)
              }
              break;
            case "雙手彎舉":
              console.log('case 雙手彎舉')
              if (_app.state.availableKeys.includes('enter')){
                console.log('case 雙手彎舉 in if')
                //window.lastTime = currentTime
                _app.keyEvent('enter', {"target":currentFoucs},_app.currentPage)
              }
              break;

            default:
              break;
          }
        } else if (json['function'] === 'getExercise') {
          console.log( 'function === getExercise', json['function'] === 'getExercise' )
          if (typeof _app.currentPage.getExerciseWebSockit === "function" ){
            _app.currentPage.getExerciseWebSockit(json)
          }
        } else {
          console.log( 'function Error ' )
        }
      };

      this.ws.onclose = function() {
        //console.log("websocket close")
        _app.ws = null
        _app.useWebSocket(_app)
      };

      this.ws.onerror = function(e) {
        console.log("onerror")
        console.log(e)
      };
    }

  }

  updateState(e){
    if (e){

      if (this.state.doExercise === false){
        var json = JSON.parse(e)
        var key = json['最後動作']
        var currentFoucs = window.$(':focus')[0]
        var currentTime = new Date().getTime()
        if (key != null && currentTime - window.lastTime > 1000){
          switch(key){
            case "左手彎舉":
              if (this.state.availableKeys.includes('s')){ // up
                window.lastTime = currentTime
                this.keyEvent('s', {"target":currentFoucs},this.currentPage)
              }
              break;
            case "右手彎舉":
              if (this.state.availableKeys.includes('x')){ // down
                window.lastTime = currentTime
                this.keyEvent('x', {"target":currentFoucs},this.currentPage)
              }
              break;
            case "雙手交叉":
              if (this.state.availableKeys.includes('esc')){
                window.lastTime = currentTime
                this.keyEvent('esc', {"target":currentFoucs},this.currentPage)
              }
              break;

            case "向左選取":
              if (this.state.availableKeys.includes('z')){ // 'left'
                window.lastTime = currentTime
                this.keyEvent('z', {"target":currentFoucs},this.currentPage)
              }
              break;

            case "向右選取":
              if (this.state.availableKeys.includes('c')){ // right
                window.lastTime = currentTime
                this.keyEvent('c', {"target":currentFoucs},this.currentPage)
              }
              break;
            case "雙手彎舉":
              if (this.state.availableKeys.includes('enter')){
                window.lastTime = currentTime
                this.keyEvent('enter', {"target":currentFoucs},this.currentPage)
              }
              break;

            default:
              break;
          }
        }
      }
    }
  }
  /*
export const SENSOR_MOVE = {
  [ '左手側平舉' ] : KEYSIM.LEFT,
  [ '右手側平舉' ] : KEYSIM.RIGHT,
  [ '雙手側平舉' ] : KEYSIM.UP,
  [ '雙手彎舉' ] : KEYSIM.SPACE,
  [ '左手彎舉' ] : KEYSIM.P,
  [ '右手彎舉' ] : PAGE.MODE,
};

  */

//cid: 1, ciid: 1
  changePage(page){
    this.setState( {page: page})

  }

  render() {
    const currentPageIndex = this.state.page;

    return (
      <div className="App" style={{maxWidth:1080, maxHeight:1920, minHeight:1920,minWidth:1080}} >
        { currentPageIndex === 1
            ? <Home util={this.keyEvent} parent={this} ></Home>
            : currentPageIndex === 2
            ? <Class2 util={this.keyEvent} parent={this} ></Class2>
            //? <Catalog util={this.keyEvent} parent={this} ></Catalog>
            //: currentPageIndex === 3
            //? <Level util={keyEvent} parent={this}></Level>
            //: currentPageIndex === 4
            //? <ClassSelect util={this.keyEvent} parent={this} ></ClassSelect>
            : currentPageIndex === 3//4
            //? <ClassIntro util={keyEvent} parent={this}></ClassIntro>
            //: currentPageIndex === 4
            ? <ClassBegin util={this.keyEvent} parent={this} ></ClassBegin>
            : <ClassDone util={this.keyEvent} parent={this} ></ClassDone> }
      </div>
    );
  }
}

export default App;
